@import url(https://fonts.googleapis.com/css2?family=Turret+Road&display=swap);
*{box-sizing:border-box;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;margin:0;padding:0}body{background-color:#d9e4dd;background-image:url("https://www.transparenttextures.com/patterns/gplay.png");min-height:70vh;padding:1rem;box-sizing:border-box;display:flex;justify-content:center;align-items:center;color:#494a4b;font-family:"Turret Road",cursive;text-align:center;font-size:130%}.container{width:100%;height:auto;min-height:500px;max-width:500px;min-width:450px;background:#f1f5f8;background-size:25px 25px;border-radius:20px;box-shadow:4px 3px 7px 2px #00000040;padding:1rem;box-sizing:border-box}.heading{display:flex;align-items:center;justify-content:center;margin:2rem 2rem}.heading h1{transform:rotate(-6deg);padding:0.2rem 1.2rem;border-radius:5px;background-color:#fdcb6e;font-size:1.5rem}.form{margin-top:3rem}.form input{box-sizing:border-box;padding:0.7rem;border:none;outline:none;font-family:"Turret Road",cursive;font-size:1rem;color:rgba(63,62,65,0.7);width:70%;margin-bottom:20px}.MuiButton-root{font-family:"Turret Road",cursive !important}.list{margin-top:1rem}ul{list-style-position:inside}ul li{text-align:left;position:relative;padding:0.5rem}.close{cursor:pointer;padding-left:9rem}

